<template lang="pug">
    .test__point 
        .test__question-info
            .test__question {{index+1}}. 
            textarea(type='text' v-model='editedQuestion.content' rows='3' :disabled='!testEditable').test__question-content
            vue-mathjax(:formula="editedQuestion.content" v-if='texTranslateVisible').formula__question
        ul.test__answers
            li.test__answer-container
                test-answer-item(
                    :answer='question.answerA'
                    :answerChar='answerChars[0]'
                    :texTranslateVisible='texTranslateVisible'
                    :testEditable='testEditable'
                    v-if='numberAnswers>0'
                    v-on:updateAnswerItem='updateAnswerItem'
                )
            li.test__answer-container
                test-answer-item(
                    :answer='question.answerB'
                    :answerChar='answerChars[1]'
                    :texTranslateVisible='texTranslateVisible'
                    :testEditable='testEditable'
                    v-if='numberAnswers>1'
                    v-on:updateAnswerItem='updateAnswerItem'

                )
            li.test__answer-container
                test-answer-item(
                    :answer='question.answerC'
                    :answerChar='answerChars[2]'
                    :texTranslateVisible='texTranslateVisible'
                    :testEditable='testEditable'
                    v-if='numberAnswers>2'
                    v-on:updateAnswerItem='updateAnswerItem'
                )
            li.test__answer-container
                test-answer-item(
                    :answer='question.answerD'
                    :answerChar='answerChars[3]'
                    :texTranslateVisible='texTranslateVisible'
                    :testEditable='testEditable'
                    v-if='numberAnswers>3'
                    v-on:updateAnswerItem='updateAnswerItem'
                )
            li.test__answer-container
                test-answer-item(
                    :answer='question.answerE'
                    :answerChar='answerChars[4]'
                    :texTranslateVisible='texTranslateVisible'
                    :testEditable='testEditable'
                    v-if='numberAnswers>4'
                    v-on:updateAnswerItem='updateAnswerItem'
                )
            li.test__answer-container
                test-answer-item(
                    :answer='question.answerF'
                    :answerChar='answerChars[5]'
                    :texTranslateVisible='texTranslateVisible'
                    :testEditable='testEditable'
                    v-if='numberAnswers>5'
                    v-on:updateAnswerItem='updateAnswerItem'
                )
            .correct
                .buttons
                    button(@click.prevent='testEditable=!testEditable' v-if='!testEditable').edit__button Редактировать
                    button(@click.prevent='updateQuestion()' v-if='testEditable').save__button Сохранить
                    button(@click='removeQuestion()' v-if='testEditable').remove__button Удалить
                    .latex__on(v-if='testEditable' )
                        input(type='checkbox' value='latexon' v-model='texTranslateVisible').tex__on
                        span.tex__on-text Включить трансляцию формул
                .correct__answer-content
                    .correct__text Правильный ответ:
                    select(v-model='editedQuestion.correctAnswer' :disabled='!testEditable').correct__answer
                        option(:value='idx' v-for='answer,idx in answerChars'  :selected='idx!==editedQuestion.correctAnswer' v-if='idx<numberAnswers') {{answer}}
</template>

<script>
import { mapActions } from "vuex";
import {VueMathjax} from 'vue-mathjax'
export default {
    components: {
        'vue-mathjax': VueMathjax,
        testAnswerItem: () => import("./test-answer-item")
    },
    props: {
        testId: Number,
        question: Object,
        index: Number,
        numberAnswers: Number
    },
    data() {
        return {
            editedQuestion: {
                id: this.question.id,
                content: this.question.content,
                answerA: this.question.answerA,
                answerB: this.question.answerB,
                answerC: this.question.answerC,
                answerD: this.question.answerD,
                answerE: this.question.answerE,
                answerF: this.question.answerF,
                correctAnswer: this.question.correctAnswer-1
            },
            selected: 0,
            disabled: 0,
            texTranslateVisible: false,
            testEditable: false,
            testInput: '',
            answerChars: ['a', 'b', 'c', 'd', 'e', 'f']
        }
    },
    mounted() {
        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.2/MathJax.js?config=TeX-AMS_HTML')
        document.head.appendChild(recaptchaScript)
    },
    methods: {
        ...mapActions("testQuestions", ["deleteTestQuestion", "updateTestQuestion"]),
        removeQuestion() {
            let data = {
                id: this.testId,
                questionId: this.question.id            
            }
            this.deleteTestQuestion(data)
        },
        updateAnswerItem(data){
            console.log(data)
            switch(data.answerChar){
                case "a":
                this.editedQuestion.answerA=data.answer
                break
                case "b":
                this.editedQuestion.answerB=data.answer
                break
                case "c":
                this.editedQuestion.answerC=data.answer
                break
                case "d":
                this.editedQuestion.answerD=data.answer
                break
                case "e":
                this.editedQuestion.answerE=data.answer
                break
                case "f":
                this.editedQuestion.answerF=data.answer
                break
            }
        },
        updateQuestion(){
            let data={
                id: this.testId,
                question: this.editedQuestion
            }
            this.updateTestQuestion(data)
            this.testEditable=!this.testEditable
        }
    }
}
</script>
